<template>
    <div class="main-content">
        <main>
            <div class="page-header">
                <div class="header-input">
                    <h1>
                        Test Dashboard
                    </h1>
                    <small>
                        Monitor key metrics:
                    <input v-on:keyup.enter="execute" id="metrics" maxlength="12" :placeholder="id_" v-model="v">
                    </small>
                </div>
                <div class="header-actions">
                    <button>
                        <span class="las la-file-export"></span>
                        Export
                    </button>
                    <button>
                        <span class="las la-tools"></span>
                        Settings
                    </button>
                </div>
            </div>
            <div class="cards">
                <div v-for="i in c_nums" :key="i" class="card-single">
                    <div class="card-flex">
                        <div class="card-info">
                            <div class="card-head">
                                <span :ref="'c'+i" :id="'c'+i">{{mac}}</span>
                                <span :ref="'n'+i" :id="'n'+i">{{sn}}</span>
                            </div>
                            <div>
                            <small>Next:</small>
                            <small :ref="'nf'+i" :id="'nf'+i">None</small>
                            </div>
                            <h2 :ref="'p'+i" :id="'p'+i">Waiting</h2>
                            <small>PASS: 123, FAIL: 10</small>
                        </div>
                        <div :ref="'q'+i" :id="'q'+i" class="card-chart success">
                            <span class="las la-fire-alt" :ref="'run'+i" :id="'run'+i"></span>
                        </div>
                        <div class="running">
                            <div class="outer">
                                <div class="body">
                                    <div class="arm behind"></div>
                                    <div class="arm front"></div>
                                    <div class="leg behind"></div>
                                    <div class="leg front"></div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div>
                        <textarea :ref="'t'+i" :id="'t'+i" placeholder="Log Message..." readonly></textarea>
                    </div>
                </div>
            </div>
        </main>
    </div>
    <label for="sidebar-toggle" class="body-label"></label>
</template>

<script>
/* eslint no-eval: 0 */
import axios from 'axios'
import qs from 'qs'

var r1 = 0; var r2 = 0; var r3 = 0; var r4 = 0; var r5 = 0; var r6 = 0

function rman (r) {
  var a
  const id = 'q' + r
  const chart = 'run' + r
  a = document.getElementById(id)
  // a.className = "las"
  // a.innerHTML = "&#xf7c5;";
  setTimeout(function () {
    a.innerHTML = "<span class='las la-fire-alt' id='" + chart + "' style='font-size:5rem;color:seagreen'> </span>"
    // a.innerHTML = '&#xf7c9'
  }, 1000)
  setTimeout(function () {
    a.innerHTML = "<span class='las la-burn' id='" + chart + "' style='font-size:5rem;color:seagreen'> </span>"
    // a.innerHTML = '&#xf7ce;'
  }, 2000)
  setTimeout(function () {
    a.innerHTML = "<span class='las la-fire' id='" + chart + "' style='font-size:5rem;color:seagreen'> </span>"
    // a.innerHTML = '&#xf7c5;'
  }, 3000)
}

function runCard (s, v) {
  if (s === 'run') {
    if (v === 'c1' && !(r1)) {
      r1 = setInterval(function () { rman('1') }, 3000)
    } else if (v === 'c2' && !(r2)) {
      r2 = setInterval(function () { rman('2') }, 3000)
    } else if (v === 'c3' && !(r3)) {
      r3 = setInterval(function () { rman('3') }, 3000)
    } else if (v === 'c4' && !(r4)) {
      r4 = setInterval(function () { rman('4') }, 3000)
    } else if (v === 'c5' && !(r5)) {
      r5 = setInterval(function () { rman('5') }, 3000)
    } else if (v === 'c6' && !(r6)) {
      r6 = setInterval(function () { rman('6') }, 3000)
    }
  } else {
    console.log('stop', v)
    if (v === 'c1') {
      clearInterval(r1)
      r1 = 0
    } else if (v === 'c2') {
      clearInterval(r2)
      r2 = 0
    } else if (v === 'c3') {
      clearInterval(r3)
      r3 = 0
    } else if (v === 'c4') {
      clearInterval(r4)
      r4 = 0
    } else if (v === 'c5') {
      clearInterval(r5)
      r5 = 0
    } else if (v === 'c6') {
      clearInterval(r6)
      r6 = 0
    }
  }
}

function run (c, s) {
  axios({
    method: 'post',
    url: 'http://127.0.0.1:8000/api/data/',
    data: qs.stringify({
      card: c,
      status: s
    }),
    headers: {
      'content-type': 'application/x-www-form-urlencoded;charset=utf-8'
    }
  })
    .then(function (response) {
      const e = response.data
      var num = e.card[1]
      const log = document.getElementById('t' + num)
      log.value = e.log
      const p = document.getElementById('p' + num)
      const nf = document.getElementById('nf' + num)
      if (p.textContent !== e.flow) {
        p.textContent = e.flow
        nf.textContent = e.nflow
      }
      if (log.value.length !== e.log.length) {
        log.scrollTo(0, log.scrollHeight)
      }
      if (e.status === 'running') {
        runCard('run', e.card)
        run(e.card, e.status)
      } else if (e.status === 'pass') {
        runCard('stop', e.card)
        c = document.getElementById(e.card)
        c.style.color = 'seagreen'
        c = document.getElementById('n' + num)
        c.style.color = 'seagreen'
      } else if (e.status === 'fail') {
        runCard('stop', e.card)
        c = document.getElementById(e.card)
        c.style.color = '#d96c4e'
        c = document.getElementById('n' + num)
        c.style.color = '#d96c4e'
        console.log('fail')
        console.log(e)
      }
    })
    .catch((error) => console.log(error))
}

export const eval2 = fn => {
  var Fun = Function
  return new Fun('return ' + fn)()
}

export default {
  name: 'metrics',
  data () {
    return {
      // metrics parameter
      id_: 'ID',
      c_nums: 8,
      v: '',
      runid: '0',
      // mac, sn
      mac: '************',
      sn: '************'
    }
  },
  mounted () {
    for (let i = 1; i <= this.c_nums; i++) {
      axios({
        method: 'post',
        url: 'http://127.0.0.1:8000/api/data/',
        data: qs.stringify({
          card: 'c' + i.toString(),
          status: 'restore'
        }),
        headers: {
          'content-type': 'application/x-www-form-urlencoded;charset=utf-8'
        }
      })
        .then(function (response) {
          run('c' + i.toString(), 'restore')
          console.log(response)
        })
        .catch((error) => console.log(error))
    }
  },
  methods: {
    execute (event) {
      if (this.id_ === 'ID' && !(eval('r' + this.v).valueOf())) {
        this.runid = this.v
        this.id_ = 'MAC'
        this.v = ''
        this.$refs['c' + this.runid].textContent = this.mac
        this.$refs['n' + this.runid].textContent = this.sn
        this.$refs['c' + this.runid].style.color = '#0120cc'
        this.$refs['n' + this.runid].style.color = '#0120cc'
      } else if (this.id_ === 'MAC') {
        this.id_ = 'SN'
        this.$refs['c' + this.runid].textContent = this.v
        this.v = ''
      } else if (this.id_ === 'SN') {
        this.id_ = 'ID'
        this.$refs['n' + this.runid].textContent = this.v
        // this.$refs['c' + this.runid].style.color = '#555'
        // this.$refs['n' + this.runid].style.color = '#555'
        this.v = ''
        run('c' + this.runid, 'start')
        // runCard('run', this.runid)
      } else {
        alert('input error')
        this.v = ''
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
